import Image from "next/image";
import React from "react";
import worldWar from "../../../public/imgs/worldware.jpg";
import Button from "../Button/Button";
export default function CardProduct() {
    const [addToCart, setAddToCart] = useState<boolean>(false);
  return (
    <>
      <figure className="flex flex-col  hover:shadow-md items-center gap-4 p-4 bg-white rounded-md hover:border hover:border-sColor">
        <Image
          alt="name of product"
          src={worldWar}
          className="w-full h-[300px] rounded-md object-contain"
        />
        <h3 className="text-xl font-[500]">world war burger</h3>
        <p className="price text-sColor font-bold ">250 Egy</p>
        <Button
          text="اضافة الي العربة"
          ariaLabel="اضافة الي العربة"
          className=" self-stretch cursor-pointer"
        />
        <div className="flex self-stretch rounded-md border border-gray-300 overflow-hidden">
          <button aria-label="decreament " className="bg-gray-300 px-4 py-2 ">
            {" "}
            -{" "}
          </button>
          <span className="number-product grow flex items-center justify-center font-bold">
            5
          </span>
          <button
            aria-label="increament  "
            className="bg-sColor text-whitep-2 px-4 py-2 "
          >
            {" "}
            +{" "}
          </button>
        </div>
      </figure>
    </>
  );
}
